import Parllax from 'parallax-js';
import Swiper, {Scrollbar, EffectCube, EffectFlip, Autoplay, Thumbs} from 'swiper';
import {WOW} from 'wowjs';


document.addEventListener('DOMContentLoaded', () => {

    new WOW().init();


    const scene = document.getElementById('scene');
    const test = new Parllax(scene); 

    const whySwiper = new Swiper('.why__slider', {
        modules: [Scrollbar, Autoplay],
        slidesPerView: 2,
        autoplay: {
            delay: 3000
        },
        scrollbar: {
            el: '.why__scrollbar',
            draggable: true,
            dragClass: 'why__scrollbar_drag'
        }

    });


    

    const histThumbs = new Swiper('.hist__thumbs', {
        modules: [Scrollbar, Thumbs],
        slidesPerView: 5,
        spaceBetween: 20,
        // centeredSlides: true,
        slideToClickedSlide: true,
        
    });

    const histSwiper = new Swiper('.hist__slider', {
        modules: [Thumbs],
        slidesPerView: 3,
        spaceBetween: 40,
        centeredSlides: true,
        slideToClickedSlide: true,
        thumbs: {
            swiper: histThumbs
        }
    });


    const testAtropos = Atropos({
        el: '.my-atropos',
        // alwaysActive: true,
        shadow: false,
        highlight: false,
        activeOffset: 30

    });

    const revSlider = new Swiper('.rev__slider', {
        modules: [Scrollbar],
        slidesPerView: 3,
        spaceBetween: 30,
        scrollbar: {
            el: '.rev__slider_scrollbar',
            dragClass: 'rev__slider_scrollbar_drag',
            draggable: true
        }
    });


    const header = document.querySelector('.header');

    document.addEventListener('scroll', () =>  {
        if(document.documentElement.scrollTop > 10) {
            header.classList.add('toggle');
        } else {
            header.classList.remove('toggle');
        }
    });
    

});





